
import { defineComponent, onMounted, reactive, toRefs, watch } from "vue";

export default defineComponent({
  name: "App",

  setup() {
    const data = reactive({
      isDisabled: true,
      isCanClick: false,
      isClicked: false,
      reactionTime: 0.0,
      currentMeas: 1,
      results: [0],
      result: 0.0,
    });

    let beginTime: number;
    let endTime: number;

    const wait = (): void => {
      data.isDisabled = true;
      data.reactionTime = 0.0;
      data.isClicked = false;
      const disableTime = Math.random() * (11 - 1) + 1;

      setTimeout((): void => {
        data.isDisabled = false;
        data.isCanClick = true;
        beginTime = new Date().getTime();
      }, disableTime * 1000);
    };

    const handleClick = (): void => {
      data.isCanClick = false;
      data.isClicked = true;
      endTime = new Date().getTime();
      data.reactionTime = (endTime - beginTime) / 1000;
      data.results.push(data.reactionTime);

      if (data.currentMeas === 5) {
        for (const res of data.results) {
          data.result += res;
        }
        data.result /= 5;
      } else {
        data.currentMeas++;
        wait();
      }
    };

    watch(
      () => data.isCanClick,
      (newValue, oldValue) => {
        if (newValue == true) {
          window.addEventListener("keyup", handleClick);
        } else {
          window.removeEventListener("keyup", handleClick);
        }
      }
    );

    onMounted((): void => {
      wait();
    });

    return {
      ...toRefs(data),
      handleClick,
    };
  },
});
